"use client";

import React from "react";
import { useParams } from "next/navigation";
import levels from "../data/modules";
import BackButton from "../../components/BackButton";
import VideoPlayer from "../../components/VideoPlayer";
import ModuleInfo from "../../components/ModuleInfo";
import ResourcesList from "../../components/ResourcesList";
import ExamButton from "../../components/ExamButton";
import Link from "next/link";

const ModulePage: React.FC = () => {
  const { id } = useParams(); // Obtener el ID de la URL
  const moduleId = Number(id); // Convertir el ID a número

  // Obtener todos los módulos desde los niveles
  const allModules = levels.flatMap((level) => level.modules);

  // Buscar el módulo correspondiente
  const module = allModules.find((m) => m.id === moduleId);

  if (!module) {
    return <p className="text-center text-red-500">Módulo no encontrado</p>;
  }

  return (
    <div className="p-6 lg:p-12 flex flex-col gap-8 bg-background min-h-screen">
      {/* Botón de regreso */}
      <BackButton onClick={() => window.history.back()} />

      <div className="grid lg:grid-cols-3 gap-8">
        {/* Columna principal */}
        <div className="lg:col-span-2 flex flex-col gap-4">
          {/* Información del módulo */}
          <ModuleInfo
            title={module.title}
            subtitle={module.subtitle}
            description={module.description}
          />

          {/* Reproductor de video */}
          <VideoPlayer videoUrl={module.videoUrl} />

          {/* Información adicional */}
          <div>
            <p className="font-bold text-sm P14">Información adicional:</p>
            <p className="text-sm P14 text-neutral-700">
              Lorem ipsum dolor sit amet consectetur. Lectus proin eget libero
              enim ullamcorper. Quam sit morbi fermentum eu sed placerat
              parturient id molestie.
            </p>
          </div>
        </div>

        {/* Columna lateral derecha */}
        <div className="flex flex-col gap-6">
          {/* Botón de test */}
          <Link href={`/dashboard/progress/module/${module.id}/exam`}>
            <ExamButton isDisabled={module.status === "locked"}>
              {module.status === "locked"
                ? "Examen no disponible"
                : "Comenzar examen"}
            </ExamButton>
          </Link>

          {/* Lista de recursos */}
          <ResourcesList
            resources={module.resources}
            showAllLink="/resources"
          />
        </div>
      </div>
    </div>
  );
};

export default ModulePage;
